/**
 * WARNING: might be fragile
 * Implementation is based on internal structure of particular version of express:
 *  v: 4.16.4
 *  https://github.com/expressjs/express/blob/4.16.4/lib/router/index.js#L53
 *
 *  unfortunately, there is no guarantee that things will stay the same
 *
 * @param node
 */


function isRouter(handle) {
    // return 'aaa'
    return Object.prototype.toString.call(handle.params) === "[object Object]"
        && Object.prototype.toString.call(handle._params) === "[object Array]"
        && Object.prototype.toString.call(handle.stack) === "[object Array]"
        ;
}

function extract(stack) {

    const list = [];

    (stack || []).forEach((s, i) => {

        const t = {}

        if (s.path) {

            t.path = s.path + '';
        }

        if (s.route && s.route.methods) {

            t.methods = Object.entries(s.route.methods)
                .filter(([key, value]) => value)
                .map(([key]) => key)
                .join(' ')
            ;
        }

        if (s.regexp) {

            t.regexp = s.regexp + '';
        }

        if (
            typeof s.name === 'string'
            && ! (["<anonymous>", "router", "bound dispatch"].indexOf(s.name) > -1)
        ) {

            t.name = s.name;
        }

        if (isRouter(s.handle || {}) || i == 13) {

            t.router = extract(s.handle.stack);
        }

        list.push(t);
    });

    return list;
}

/**
 * {
 *     app: object (express app instance)
 *     secure: function
 * }
 * @param opt
 * @returns {Function}
 */
module.exports = opt => {

    if ( Object.prototype.toString.call(opt.app) !== "[object Function]" ) {

        throw "extractRoutesMiddleware error: opt.app is not specified";
    }

    return (req, res) => {

        if (typeof opt.secure === 'function') {

            if ( ! opt.secure(req, res)) {

                return res.status(403).json({
                    error: 'no access',
                });
            }
        }

        const list = extract(opt.app._router.stack);

        res.set({
            'Cache-Control' : 'no-store, no-cache, must-revalidate',
            'Pragma'        : 'no-cache',
            'Content-type'  : 'application/json; charset=utf-8',
            'Expires'       : new Date().toUTCString(),
        }).json(list);
    };
}