
let cache;

module.exports = (req, res, next) => {

    // log(req.query); // add get parameter ?_accept

    let force = false;

    try {
        if (typeof req.query._accept !== 'undefined') {
            force = true;
        }
    }
    catch (e) {

    }

    cache = req.get('accept') || '';

    if ( ! ( cache.toLowerCase().indexOf('text/html') > -1) && ( ! force ) ) {

        return res.status(400).end(`No 'text/html' in 'accept' header : ${cache}`);
    }

    next();
}
